import {
    ActionType,
    ChangeDiscoverPostOpenStateAction,
    GetPostAction,
    PostPositionChangeAction,
    SetPostAction
} from "@Types/redux/actions";
import Posts from "@Types/model/Posts";

export function getPostList(hashTag: string): GetPostAction {
    return {
        type: ActionType.GET_POST,
        hashTag
    }
}

export function setPostList(posts: Posts[]): SetPostAction {
    const totalPage = posts.length / 50;
    const currentPage = Math.floor(Math.random() * totalPage);

    const pagePosts = posts.splice(50 * currentPage, 50);

    return {
        type: ActionType.SET_POST,
        posts: pagePosts
    }
}

export function changePostPosition(position: number): PostPositionChangeAction {
    return {
        type: ActionType.POST_POSITION_CHANGE,
        position
    }
}

export function changeDiscoverPostOpenState(isPostOpen: boolean, position?: number): ChangeDiscoverPostOpenStateAction {
    return {
        type: ActionType.CHANGE_DISCOVER_POST_OPEN_STATE,
        isPostOpen,
        position
    }
}
