import React from "react";
import videoJs from "video.js";
import "./Video.less";
import ReactGa from "react-ga";

interface State {
}

interface Props {
    videoUrl: string;
    posterUrl: string;
    className?: string;
    onVideoEnd?: () => void;
}

export default class Video extends React.Component<Props, State> {
    public player?:videoJs.Player;
    private videoNode?: HTMLVideoElement;

    componentDidMount(): void {
        const { videoUrl, onVideoEnd, posterUrl } = this.props;
        const options = {
            autoplay: true,
            controls: true,
            inactivityTimeout: 500,
            poster: posterUrl,
            preload: "auto",
            sources: [{
                src: videoUrl,
                type: "video/mp4"
            }],
            controlBar: {
                fullscreenToggle: false
            }
        };

        this.player = videoJs(this.videoNode, options, () => {
            if (this.player) {
                this.player.userActive(false);
                this.player.on("ended", () => {
                    onVideoEnd && onVideoEnd();
                });
                let value = 0;
                this.player.on("enterpictureinpicture", () => {
                    ReactGa.event({
                        category: "video",
                        action: "pip-open",
                        label: "video"
                    });
                    value++;
                });

                this.player.on("leavepictureinpicture", () => {
                    ReactGa.event({
                        category: "video",
                        action: "pip-close",
                        label: "video"
                    });
                });

                this.player.on("loadedmetadata", () => {
                    if (this.player && value === 0) {
                        (this.player.$(".vjs-picture-in-picture-control") as HTMLElement).click();
                    }
                });
            }
        });
    }

    componentDidUpdate(prevProps: Readonly<Props>, prevState: Readonly<State>, snapshot?: any): void {
        if (this.player) {
            this.player.poster(this.props.posterUrl);
            this.player.src({
                src: this.props.videoUrl,
                type: "video/mp4"
            });
            ReactGa.event({
                category: "video",
                action: "video-change",
                label: "video"
            });
        }
    }

    componentWillUnmount(): void {
        if (this.player) {
            this.player.dispose();
        }
    }

    render() {
        return (
            <div className={`${this.props.className} video-koko`}>
                <div data-vjs-player>
                    <video ref={(node: HTMLVideoElement) => this.videoNode = node} className="video-js" />
                </div>
            </div>
        );
    }
}
